import { Resend } from 'resend';
import { NextRequest, NextResponse } from 'next/server';

// Lazy initialization to avoid build-time errors
let resend: Resend | null = null;

function getResend(): Resend {
  if (!resend) {
    if (!process.env.RESEND_API_KEY) {
      throw new Error('RESEND_API_KEY environment variable is not set');
    }
    resend = new Resend(process.env.RESEND_API_KEY);
  }
  return resend;
}

const ADMIN_EMAIL = 'info@thermodesk.eu';
const FROM_EMAIL = 'ThermoDesk <noreply@thermodesk.eu>';

interface DemoEmailData {
  type: 'demo';
  name: string;
  email: string;
  company?: string;
  phone?: string;
  company_size?: string;
  message?: string;
}

interface ContactEmailData {
  type: 'contact';
  name: string;
  email: string;
  subject?: string;
  message: string;
}

interface NewsletterEmailData {
  type: 'newsletter';
  email: string;
}

type EmailData = DemoEmailData | ContactEmailData | NewsletterEmailData;

export async function POST(request: NextRequest) {
  try {
    const data: EmailData = await request.json();

    switch (data.type) {
      case 'demo':
        await sendDemoEmails(data);
        break;
      case 'contact':
        await sendContactEmails(data);
        break;
      case 'newsletter':
        await sendNewsletterEmail(data);
        break;
      default:
        return NextResponse.json(
          { error: 'Invalid email type' },
          { status: 400 }
        );
    }

    return NextResponse.json({ success: true });
  } catch (error) {
    console.error('Email sending error:', error);
    return NextResponse.json(
      { error: 'Failed to send email' },
      { status: 500 }
    );
  }
}

